.con_user_img .mCS_img_loaded{
    display: block;
    background-size: cover;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: #f7f7f7;
    max-width: inherit;
}

.con_user_img i{
    line-height: 20px;
    text-align: center;
}

.feed_steps li a.step_complete span:after {
    content: '';
    position: absolute;
    left: -2px;
    right: 0;
    top: -2px;
    background-image: url(data:image/png;base64,);
    background-color: #fff;
    border-radius: 50%;
    width: 22px;
    height: 22px;
    background-position: center;
    background-size: contain;
  }

  input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.border-hover{
  transition: all 0.3s linear;
}
.show-border a{
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.border-bottom span.active{
  border-bottom: 2px solid #FF4672;
}
.border-bottom span{
  cursor: pointer;

}
.bg-color li .active {
  background-color: #edf0f4 !important;
  color: #FF4672 !important;
}
.contect-tabs li a{
  font-size: 14px;
}
.apexcharts-datalabels-group{
  transform: translate(0, -8px) !important;
}